body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navigator .Navigator__Items {
  display: -webkit-flex;
  display: flex; }
  .Navigator .Navigator__Items .Navigator__Item {
    position: relative;
    max-width: 160px;
    height: 40px;
    padding: 0 10px;
    box-shadow: 1px 1px 1px #f5f5f5;
    text-align: center; }
    .Navigator .Navigator__Items .Navigator__Item .Navigator__Item__Title {
      line-height: 40px;
      font-size: 16px;
      font-weight: bold; }
    .Navigator .Navigator__Items .Navigator__Item:after {
      content: '';
      position: absolute;
      right: 0px;
      top: 5px;
      height: 30px;
      line-height: 40px;
      border-right: 1px solid grey; }

.GlobalHeader {
  width: 100%;
  background-color: #F6F8F9; }
  .GlobalHeader .GlobalHeader__Navigator {
    padding: 5px 0; }

.LoadingProgress {
  height: 100vh;
  margin: 0 auto; }

